body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  background: #eee;
  color: rgba(0, 0, 0);
  font-family: 'Droid Serif', serif;
  position: relative;
}

.app.dark {
  background: #1b1b1d;
  color: rgba(238, 238, 238, 0.9);
}

.app-wrapper {
  background: inherit;
  padding: 0 10vw;
  display: grid;
  grid-template-rows: auto auto 100vh auto;
  position: relative;
}

.title, .app-header, .app-footer {
  text-align: center;
}

.title {
  font-family: 'Trebuchet MS', Arial, sans-serif;
  font-weight: 700;
  font-size: 68px;
  display: inline-block;
  line-height: 79px;
  padding: 0 10px;
  width: 100%;
}

.app-header {
  background: inherit;
  z-index: 10;
  position: sticky;
  position: -webkit-sticky;
  top: 0px;
  height: 50px;
  padding-bottom: 16px;
}

.app-body {
  display: flex;
  flex-direction: row;
  position: relative;
  width: 100%;
  padding: 50px 0;
}

.articles {
  width: 70%;
  padding: 0 15px;
  border-right: 1px solid #79797957;
  flex: 0 0 auto;
}

@media only screen and (max-width:768px) {
  .app {
    font-size: 20px;
  }
  .app-wrapper {
    padding: 0 15px;
  }

  .app-body {
    flex-direction: column;
  }

  .articles {
    padding: 15px 0;
    border-right: none;
    border-bottom: 5px double #79797957;
    width: 100%;
  }

}



.header {
    text-transform: uppercase;
    border-bottom: 2px solid;
    border-top: 2px solid;
    padding: 10px 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.switch {
  display: block;
  position: relative;
 	width: 50px;
  height: 28px;
  border-radius: 25px;
  background: rgba(86, 65, 126, 0.5);
  -webkit-tap-highlight-color: transparent;
  border: 1px solid;
  cursor: pointer;
  border-collapse: separate;
  box-shadow:
    inset 0px 0px 2px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
  -webkit-box-shadow:
    inset 0px 0px 2px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
  -moz-box-shadow:
    inset 0px 0px 2px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
  transition: background 0.4s ease-out;
}

.switch.off {
  background: #EF810E;
}

#switch {
 	clip: rect(0 0 0 0);
  position: absolute;
  visibility: hidden;
}

.slider {
  box-sizing: border-box;
  height: 20px;
  width: 20px;
  background: #fff;
  border: 1px solid black;
  border-radius: 50%;
  position: absolute;
  top: 4px;
  left: 25px;
  border-collapse: separate;
  box-shadow: 0px 0px 5px; 
  -webkit-box-shadow: 0px 0px 5px;
  -moz-box-shadow: 0px 0px 5px;
  transition: all 0.4s ease-out;
}

.slider.off {
  left: 5px;
} 
